@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-sortbox{
	.relative(0,_,_,_);

	.filters{
		background-color: #fff;
		position: relative;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 1*@rex solid @bd-color;
        z-index: 101;

        .col{
        	flex: 1;
			margin:10*@rex 0;
        	&.red{
                color:@ht-color;
            }

            &:first-child{
            	border-right:1*@rex solid @bd-color;
            }

            .icon{
            	.inline-block();
            	position: relative;
            	transition:transform .3s ease;
            }
            &.active .icon{
				transform:rotate(180deg);
        	}
        }
	}

	.filter-left{
		background: #fff;
		width: 100%;
		max-width: @max-width;
		.absolute(42*@rex,_,_,_);

		.filter-list{
			margin-left: 10*@rex;
            color:#666;
            li{
                line-height: 2.4;
                border-bottom: 1*@rex solid @bd-color;

                &:last-child{
                    border-bottom:none;
                }
                i {
                	display: none;
                	float: right;
                	color: @ht-color;
                	padding-right: 10*@rex;
                }
                &.active i {
                	display: block;
                }
            }
		}
	}
	.filter-right{
		background: #fff;
		width: 100%;
		max-width: 800*@rex;
		position: absolute;
		top:0;
		font-size:unit(@price-size-rmb-md,rem);
		.wrapper{
			padding:7*@rex 5*@rex;
			overflow: hidden;
			span{
				line-height: 2;
                input{
                    width: 100%;
                    line-height: 2;
                    padding-left: 5*@rex;
                    border: 1*@rex solid @bd-color;
                    border-radius: 0;
                    box-shadow: 0;
                    outline: none;
                    appearance:none;
                    outline:none;
                }
                &.hr{text-align: center; float: left;width: 5%;}
                &.price_word{
                    float: left;
                    width: 35%;
                    text-indent: 5*@rex;
                }
                &.input_box{
                    width: 20%;
                    float: left;
                }
                &.btn_box{
                    width: 20%;
                    text-align: right;
                    float: left;

                    .btn{
                    	display: block;
                    	float: right;
	                    color: #fff;
	                    width: 45*@rex;
	                    line-height: 2;
	                    text-align: center;
	                    border-radius: 5*@rex;
	                    background: @ht-color;
                    }
                }
            }
		}
	}
	.load_mask{
	    .fixed(0,_,_,0);
	    z-index: 99;
	    .size(100%);
	    display: none;
	    background-color: rgba(0, 0, 0, 0.8);
	}
	&.active{

		.filter-left,.filter-right{
			z-index:101;
		}
		.load_mask{
			display: block;
		}


	}

}


