@import "./_store-variables.less";
@import "./_store-mixins.less";
@import './module/fn-store-bottom-nav.less';

.iconfont-face(@store-icon-name, @store-icon-url);
.@{container-class}{
    padding-bottom: 55px;
}

.@{store-icon-name}{
	.iconfont-mixin(@store-icon-name);
}

.iconfont{    line-height: inherit;}
.mt2{    margin-top: 2%;}

.@{container-class} {
	background:#eee;
	i{
		font-style: normal;
	}
	.header{


		height: unit(44 + 1, px);
		line-height: unit(44, px);
		background: #fff;
		position: relative;
		font-size: 18*@rex;
		border-bottom: 1*@rex solid @bd-color;

		.top-back{
			.absolute(0,_,_,2%);

			span {
				font-size: 16*@rex;
				vertical-align: top;
				display: inline-block;
			}

			i {
				line-height: 44*@rex;
				font-size: 24*@rex;
				vertical-align: top;
				display: inline-block;
				margin-top: -2*@rex;
			}
		}

		.title{
			width: 60%;
			font-size:100%;
			font-weight:normal;
			margin: 0 auto;
			text-align: center;
		}
		.search-box{
			width: 60%;
			height:70%;
			margin:0 auto;
			border-radius: 5*@rex;
			.relative(15%,_,_,2%);
			background: #eee;
			display: flex;
			color:#666;
			i{
				font-size:20*@rex;
				line-height: 1.4;
				float: left;
				width:30*@rex;
				text-align: right;
			}
			input{
	            flex: 1;
	            border: 0;
	            text-indent: 5*@rex;
	            margin-right:5*@rex;
	            background:inherit;
	            outline: none;
	        }
		}
		.h_share{
			.absolute(0,2%,_,_);
			font-size:22*@rex;
		}
	}

	.rb-nav{
		.fixed(_,20*@rex,58*@rex,_);
		z-index: @rb-nav-zindex;

		.go-top{
			display: block;
			line-height: .8rem;
			.size(.8rem);
			font-size: .6rem;
			border:1*@rex #8f8f8f solid;
			background: rgba(255,255,255, 0.8);
			border-radius: .8rem;
			text-align: center;
		}

	}

	.header{
		@height: 45;

	.more-info{
		height: unit(@height, px);
		line-height: unit(@height, px);
		.absolute(0,0,_,_);
		margin-right: 3%;
		font-size:  24px;
	}

	.more-sub{
		.absolute(32px, 3%,_, _);
		.size(200px);
		padding-top: 10px;
		margin-right: -8px;
		width: 40%;
		z-index: -1;
		opacity: 0;
		transition: opacity .2s;

		&-show{
			opacity: 1;
			z-index: 100;
		}

		&:before{
			display: block;
			content: '';
			overflow: hidden;
			font-size: 0;
			line-height: 0;
			border-color: transparent transparent rgba(65,70,72,0.9) transparent;
			border-style: dashed dashed solid dashed;
			border-width: 10px;
			.size(0);
			.absolute(-10px,8px,_,_);
		}

		.item{
			line-height: 2.8;
			display: block;
			border-bottom: 1px solid rgba(255,255,255,0.2);
			background: rgba(65,70,72,0.9);
			color: #d5d5d5;
			font-size: 14px;

			&:nth-of-type(1){
				border-radius: 6px 6px 0 0;
			}

			&:nth-last-of-type(1){
				border-radius: 0 0 6px 6px;
				border-bottom: none;
			}

			i{
				font-size: 16px;
				padding: 0 5px;
			}
		}
	}

	}


}
