@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-loading-btn{
    text-align: center;
    padding: 10*@rex 0;
    font-size: 14*@rex;

    .loading-img{
    	margin-right: 5*@rex;
	    display: inline-block;
      .size(20*@rex);
	    vertical-align:middle;
	    overflow: hidden;
	    background: data-uri('@{store-image-path}/loading_txt.png') center center no-repeat;
	    -webkit-background-size: 0.44rem 0.44rem;
	    background-size: 20*@rex 20*@rex;

	    &:before{
	    	display: block;
        .size(100%);
		    vertical-align: middle;
		    content: "";
		    background: data-uri('@{store-image-path}/loading.png') center center no-repeat;
		    background-size: 20*@rex 20*@rex;
		    animation: loading_rotate 0.8s linear infinite;
	    }
    }
}

@-webkit-keyframes loading_rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes loading_rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}