@import "../common/mixins.less";
@import "../common/widget/toast.less";

.iconfont-face(@iconfont-family, @iconfont-url);

.@{icon-name}{
	.iconfont-mixin(@iconfont-family);
}

.store-item-list () {
	.product-title{
		line-height: 2;
		text-align: center;
		margin: 0 2.4%;
		position: relative;

		&:before{
			.absolute(50%,_,_,0);
			width: 100%;
			height: 1*@rex;
			content: '';
			background: @bd-color;
		}

		span{
			font-size:16*@rex;
			display: inline-block;
			vertical-align: top;
			padding: 0 8*@rex;
			background: @container-bg-color;
			color: #666;
			position: relative;
		}
	}

	.item-list{
		.item{
			background: #fff;

			.pic{
				display: block;
			}

			.title{
				a{
					overflow: hidden;
					margin: 5*@rex 10*@rex;
					height:unit(1.42858 * @price-size-num-md * 2,rem);
					.ellipsis(2);
					color: #333;
				}
			}

			.price{
				padding-bottom:10*@rex;
				overflow: hidden;

				.now,.primary{
					max-width: 42%;
					overflow: hidden;
					display: inline-block;
					margin-left:10*@rex;
					vertical-align: bottom;
					line-height: 1;
				}

				.primary{
					color: #969696;
					text-decoration: line-through;
					max-width: 38%;
				}

				.vip{
					min-width: 20%;
					height: 20*@rex;
					display: inline-block;
				}

				.vip-txt{
					line-height: 1;
					display: inline-block;
					vertical-align: bottom;
					margin-left: 10*@rex;
					color: #E71B2B;
					border: 1*@rex solid #E71B2B;
					padding: 1*@rex 2*@rex;
					border-radius: 3*@rex;
					font-size: 12*@rex;
				}
				.goods-preparing{
					max-width: 38%;
					overflow: hidden;
					white-space: nowrap;
					line-height: 1;
					float: right;
					margin-right: 10*@rex;
					font-size: 13*@rex;
					color: #999;
					position: relative;
					top: 5*@rex;
				}
			}
		}
	}
}
