@import "../_store-variables.less";
@import "../_store-mixins.less";

.fn-@{store-ns}-bottom-nav{
	.fixed(_,0,0,0);
	width: 100%;
	padding: 6*@rex 0;
	max-width: 1024px;
	z-index: 200;
	transform: translateZ(0);
	background: #fff;
	border-top: 1*@rex solid @bd-color;
	margin: 0 auto;

	.item{
		float: left;
		text-align: center;
		padding: 3*@rex 0;
		font-size: 12*@rex;
		display: block;
		position: relative;

		span{
				padding-top: 5*@rex;
				display: block;
			}

		.line{

			&:before, &:after{
				display: block;
			    content: '';
			    .size(24*@rex,2*@rex);
			    background: @bd-color;
			    margin:0 auto 2*@rex;
			}
		}


		.item-list{
			width: 90%;
			.absolute(_,_,50*@rex,4.8%);
			border: 1*@rex solid @bd-color;
			border-radius: 5*@rex;
			z-index: 1;
			background: #fff;
			color:#333;
			display:none;

			&:before, &:after{
				display: block;
			    content: '';
			    border-style: solid solid dotted solid;
			    border-width: 5*@rex;
			    .size(0*@rex,0*@rex);
			    margin-left: -5*@rex;
			}

			&:before{
			    border-color:  @bd-color transparent transparent transparent;
			    .absolute(_,_,-11*@rex,50%);
			 }

			 &:after{
			    border-color:  #fff transparent transparent transparent;
			    .absolute(_,_,-10*@rex,50%);
			}

			.list{
				padding: 10*@rex 0;
				text-align: center;
				border-bottom: 1*@rex solid @bd-color;
				position:relative;
				display: block;

				&:last-child{
					border-bottom: 0;
				}
			}
		}

		&-theme{
			color: @ht-color;
		}

		&-active{

			.item-list{
				display: block;
			}
		}
	}
}

